import React from "react";
import { Container, Row, Col } from "react-bootstrap";
import SecIntro from "./SecIntro";
import WhyChooseUsIco from "../assets/images/whyChooseUs.svg";
import PageTopBanner from "./PageTopBanner";

const contentData = {
  heading:
    "Licence now is an online platform connecting learner drivers to their local driving instructor.",
  paragraph: [
    "Licence Now makes it easy to find, view and book driving instructor’s online. We are dedicated to providing you with choice and making your booking experience smooth and efficient with your chosen instructor. ",
    "We have partnered with multiple driving instructors, across multiple suburbs, to bring you a simple way to book, and manage your driving lessons ensuring you the best experience possible when learning to drive.",
    "Simply search your preferred suburb and transmission type online, and you can compare all partnered driving instructors profiles in your chosen area. ",
    "All profiles include:",
  ],
  listItems: [
    "Photo of Instructor",
    "Their experience ",
    "Bio",
    "Languages spoken by instructor",
    "Ratings and reviews by learners",
    "Real time calendar availability",
    "Details of their vehicle",
  ],
  bottomDesc: [
    "Learner drivers and parents are assured that all driving instructors listed on the Licence Now platform are licenced and hold a valid working with children check.",
    "Our platform makes it easy for you to choose an instructor, and book driving lessons online. No more searching through multiple schools for availability and preferences. <br> Simply search your preferred area and transmission type, choose and instructor, and book directly into their calendar in real time.<br>The instructor then simply arrives at your chosen location at the selected time, and conducts your driving lesson.",
    "Any future lessons, cancellations/reschedules are made via your online dashboard which is created during your initial booking. From here you can book more lessons, cancel or reschedule existing lessons, and even change driving instructors! No questions asked!",
    "<strong>We look forward to seeing you out there!</strong> ",
  ],
};

const introData = {
  titleStart: "Who is ",
  highlight: "Licence Now?",
  titleEnd: "",
  theme: "",
};

function AboutUs() {
  return (
    <>
      <PageTopBanner title="About Us" />
      <div className="WhyChooseUs bg-white">
        <Container>
          <Row>
            <Col lg={12}>
              {/* section intro block */}
              <SecIntro
                titleStart={introData.titleStart}
                highlight={introData.highlight}
                titleEnd={introData.titleEnd}
                theme={introData.theme}
              />
              <div className="WhyChooseUs__wrp">
                <div className="WhyChooseUs__left">
                  <h4>{contentData.heading}</h4>
                  {contentData.paragraph.map((item, index) => (
                    <p key={index}>{item}</p>
                  ))}
                  <div className="WhyChooseUs__points">
                    <ul>
                      {contentData.listItems.map((item, index) => (
                        <li key={index}>{item}</li>
                      ))}
                    </ul>
                    <div className="WhyChooseUs__bottomDesc">
                      {contentData.bottomDesc.map((item, index) => (
                        <p
                          key={index}
                          dangerouslySetInnerHTML={{ __html: item }}
                        ></p>
                      ))}
                    </div>
                  </div>
                </div>
                <div className="WhyChooseUs__right">
                  <img src={WhyChooseUsIco} alt="" />
                </div>
              </div>
            </Col>
          </Row>
        </Container>
      </div>
    </>
  );
}

export default AboutUs;
